.detail{
    background: #ffffff;
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px rgba(0, 0, 0, 0.3),0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
.detail a {
    display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
}
.detail img{
    position: relative;
    top: -10px;
    max-width: 300px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px rgba(0, 0, 0, 0.3),0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
.detail .movie__title, .detail .movie__year {
    margin: 0;
    font-weight: 300;
}

.detail .movie__title{
    margin-bottom: 5px;
    font-size: 24px;
    color: 2c2c2c;
}
.detail .movie__genres{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0;
}
.movie__genres li, .detail .movie__year {
    margin-right: 10px;
    font-size: 14px;
}
.detail {
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    margin: 50px auto;
    padding-top: 70px;
    background-color: #efefef;
}
